import { Historical } from './../pages/historical/historical';
import { Component ,ViewChild,Inject } from '@angular/core';
import { Platform, ToastController, AlertController, MenuController, NavController, App, ActionSheetController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HomePage } from '../pages/home/home';
import { Storage } from '@ionic/storage';
import { GestPoi } from '../pages/gestpoi/gestpoi';
import {Vehicules} from '../pages/datamana/vehicules/vehicules';

@Component({
  templateUrl: 'app.html'
})

export class MyApp {
  // integration du navController dans app.component.ts
  @ViewChild('mycontent') nav:NavController
  rootPage;
  listhide:boolean=true;

  constructor(platform: Platform, statusBar: StatusBar,private _app:App,public actionSheetCtrl: ActionSheetController, private menuCtrl:MenuController,private alertCtrl :AlertController,private storage:Storage, splashScreen: SplashScreen,public toastCtrl:ToastController) {
    platform.ready().then(() => {
      //alert de confirmation de quitter l'application
      platform.registerBackButtonAction(()=>{
        let alert = this.alertCtrl.create({
          title: 'Attention',
          message: 'Voulez vous vraiment quitter ?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                
              }
            },
            {
              text: 'Quitter',
              handler: () => {
                platform.exitApp();
              }
            }
          ]
        });
        alert.present(); 
      });
      //check previous signing
      this.checkPreviousAuthorization();
      splashScreen.hide();
      statusBar.show(); 
      statusBar.styleDefault();
    });  
  }
  // fermer menu
  menuclose(){
    this.menuCtrl.close();
    this.listhide=true;
  }
  //list getion de donne
  hidelist(){
    this.listhide=!this.listhide;
  }
  //navigation to gestion Poi
  GotoGestpoi(){
    this.nav.push(GestPoi);
    this.menuclose();
  }
  //navigation to  temps reel et historique
  GotoTempsRealandHistorique(){
    this._app.getRootNav().setRoot(Historical);  
    this.menuclose();
  }
  //navigation to gestion de vehicule
  GoToGestVehi(){
    this.nav.push(Vehicules);
    this.menuclose(); 
  }
  //verification d'authentification
  checkPreviousAuthorization(): void { 
    if((window.localStorage.getItem('currentuser') === "undefined" || window.localStorage.getItem('currentuser') === null)) {
      this.rootPage = HomePage;
    } else {
      this.rootPage = Historical;
    }
    
  }
  //alert deconnexion
  logoutconfirm() {
    this.menuclose();
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Souhaitez-vous vraiment vous dconnecter?',
      buttons: [
         {
          text: 'Annuler',
          role: 'cancel',
          handler: () => {
            
          }
        },{
          text: 'Dconnexion',
          handler: () => {
          this.logout();
          }
        }
      ]
    });
    actionSheet.present();
  }
  //deconnexion
  logout() : void {
    localStorage.removeItem('currentuser');
    this._app.getRootNav().setRoot(HomePage);       
  }
  //end
}




